.custom_margin{
  margin: 80px;
}
  .form-style input{
    border:0;
    height:50px;
    border-radius:0;
  border-bottom:1px solid #ebebeb;	
  }
  .form-style input:focus{
  border-bottom:1px solid #007bff;	
  box-shadow:none;
  outline:0;
  background-color:#ebebeb;	
  }
  .sideline {
      display: flex;
      width: 100%;
      justify-content: center;
      align-items: center;
      text-align: center;
    color:#ccc;
  }
  button{
  height:50px;	
  }
  .sideline:before,
  .sideline:after {
      content: '';
      border-top: 1px solid #ebebeb;
      margin: 0 20px 0 0;
      flex: 1 0 20px;
  }
  
  .sideline:after {
      margin: 0 0 0 20px;
  }

  .random_img{
     padding: 20px; 
     border-radius: 40px;
  }

  @media only screen and (max-width: 769PX) {

    .custom_margin{
      margin:80px 0px
    }
  }